/* stylelint-disable */
@font-face {
  font-family: "Popins-reg";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Popins-semb";
  src: url("../fonts/../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.burgundy-btn {
  color: #FFFFFF;
  border-radius: 8px;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.burgundy-btn:hover {
  background: #521424;
}

.orange-btn {
  border-radius: 8px;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  color: black;
  transition: background-color 0.3s ease;
}
.orange-btn:hover {
  background: #C26226;
}

.grey-btn {
  border-radius: 8px;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
}

[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "Popins-reg", sans-serif;
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: #111111;
  background: transparent;
}
body._lock {
  overflow: hidden;
}

.casino-review {
  background: #FCEDF0;
  margin: 0 0 20px 0;
}
.casino-review._onWhite {
  padding: 15px;
  border-radius: 8px;
}
@media screen and (max-width: 350px) {
  .casino-review._onWhite {
    padding: 5px;
  }
}
.casino-review__all-link {
  display: inline-block;
  font-family: "Popins-semb", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  align-self: center;
  text-align: center;
  padding: 19px 85px 17px;
}
.casino-review__container {
  display: flex;
  flex-direction: column;
  padding: 0;
}
.casino-review__list {
  display: grid;
  grid-template-columns: calc(33.333% - 10px) calc(33.333% - 10px) calc(33.333% - 10px);
  justify-content: start;
  gap: 15px;
  margin: 0 0 30px 0;
}
.casino-review__item {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background: #FFFFFF;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  width: 100%;
}
.casino-review__item-info {
  display: flex;
  align-items: center;
  margin: 0 0 10px 0;
}
.casino-review__item-image {
  max-width: 160px;
  margin: 0 20px 0 0;
}
.casino-review__item-image img {
  border-radius: 8px;
}
.casino-review__item-rtp-percent {
  font-family: "Popins-semb", sans-serif;
  font-size: 20px;
  line-height: 16px;
  letter-spacing: -0.004em;
  color: #691A2E;
  margin: 0 0 5px 0;
}
.casino-review__item-rtp-text {
  max-width: 70px;
  font-size: 11px;
  line-height: 14px;
  color: #111111;
}
.casino-review__item-title {
  font-size: 17px;
  line-height: 26px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 2px 0;
}
.casino-review__item-licensed {
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  color: #2275D4;
  margin: 0 0 10px 0;
}
.casino-review__item-licensed-one {
  margin: 0 8px 0 0;
}
.casino-review__item-review {
  display: flex;
  align-items: center;
  margin: 0 0 auto 0;
}
.casino-review__item-review-star, .casino-review__item-review-comment {
  display: flex;
}
.casino-review__item-review-star img, .casino-review__item-review-comment img {
  display: inline-block;
  margin: 0 5px 0 0;
}
.casino-review__item-review-text {
  font-size: 12px;
  line-height: 12px;
  color: #111111;
}
.casino-review__item-review-star {
  margin: 0 10px 0 0;
}
.casino-review__item-list {
  display: flex;
  flex-direction: column;
  margin: 20px 0 20px 0;
}
.casino-review__item-item {
  padding: 8px 0;
  border-top: 1px solid #E7EAEE;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.casino-review__item-item:last-child {
  border-bottom: 1px solid #E7EAEE;
}
.casino-review__item-item-title {
  font-size: 13px;
  line-height: 20px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 10px 0 0;
}
.casino-review__item-item-info {
  font-family: "Popins-semb", sans-serif;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: -0.004em;
  color: #111111;
}
.casino-review__item-buttons {
  display: flex;
  align-items: center;
}
.casino-review__item-btn {
  width: calc(50% - 5px);
  display: inline-block;
  text-align: center;
  padding: 12px 10px 10px;
  font-size: 15px;
  line-height: 20px;
}
.casino-review__item-btn:not(:last-child) {
  margin: 0 10px 0 0;
}

@media screen and (max-width: 1240px) {
  .casino-review__list {
    display: grid;
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
    gap: 15px;
    margin: 0 0 30px 0;
  }
}
@media screen and (max-width: 600px) {
  .casino-review__all-link {
    width: calc(100% - 30px);
  }
  .casino-review__list {
    grid-template-columns: 100%;
    padding: 0;
    gap: 10px;
    margin: 0 0 20px 0;
  }
  .casino-review__item {
    padding: 20px;
  }
  .casino-review__item-list {
    display: grid;
    grid-template-columns: calc(50% - 5px) calc(50% - 5px);
    gap: 10px;
  }
  .casino-review__item-item {
    border-top: 1px solid #E7EAEE;
  }
  .casino-review__item-item:last-child, .casino-review__item-item:nth-child(3) {
    border-bottom: 1px solid #E7EAEE;
  }
}